.ait {
  background-color: var(--bg-darkest);
  color: white;
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
}

.ail-container {
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.ail-body {
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  padding: 0 4rem 1rem;
  overflow-y: auto;
  height: 100%;
}

.ail-header,
.ail-footer {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}

@media screen and (max-width: 767px) {
  .ail-body {
    padding: 0 1rem;
  }
}

.ail-footer {
  display: -ms-flexbox;
  display: flex;
}


