.ail-query {
  border: none;
  padding: 1rem;
  height: 2rem;
  width: 100%;
  resize: none;
}

.ail-search {
  max-width: 10rem;
  width: 20%;
  border: none;
  background: var(--bg-secondary);
  font-size: 1.2rem;
  color: white;
  cursor: pointer;
}

.ail-search:hover {
  opacity: .95;
}


