.ail-footer-status {
  font-family: 'Courier New', Courier, monospace;
  font-size: .8rem;
  border-top: 1px solid var(--border-dark);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.ail-footer-status-item {
  padding: 0 0.5rem;
}

.ail-footer-status--timestamp {
  background: green;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}

.ail-footer-status--error {
  background: red;
}

@media screen and (max-width: 767px) {
  .ail-footer-status {
      -ms-flex-flow: column;
          flex-flow: column;
  }
}


