.ail-header {
  font-size: 1.2rem;
  padding: 1.3rem 4rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid var(--border-dark);
  position: relative;
}

.ail-header-title {
  color: white;
  text-decoration: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: baseline;
      align-items: baseline;
}

.ail-header-app_name {
  position: absolute;
  right: 50%;
  -webkit-transform: translateX(50%);
      -ms-transform: translateX(50%);
          transform: translateX(50%);
  bottom: -0.6rem;
  background: var(--bg-secondary);
  color: var(--bg-default);
  padding: .1rem .8rem;
  border-radius: 11px;
  font-size: .9rem;
  opacity: .2;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  max-width: 80%;
  white-space: nowrap;
  -webkit-transition: opacity ease-in .3s;
  -o-transition: opacity ease-in .3s;
  transition: opacity ease-in .3s;
}

.ail-header-app_name.active,
.ait:hover .ail-header-app_name {
  opacity: 1;
}


@media screen and (max-width: 767px) {
  .ail-header {
    padding: 1.3rem 1rem;
  }
  .ail-body {
    padding: 0 1rem;
  }
}

/* Title icon */
@-webkit-keyframes bulb {
  0% {
    background-color: transparent;
    -webkit-box-shadow: 0;
            box-shadow: 0;
    opacity: 0;
  }
  75% {
    -webkit-box-shadow: 0px 0px 20px 20px yellow;
            box-shadow: 0px 0px 20px 20px yellow;
    background-color: yellow;
    opacity: .2;
  }
  100% {
    background-color: transparent;
    -webkit-box-shadow: 0;
            box-shadow: 0;
    opacity: 0;
  }
}
@keyframes bulb {
  0% {
    background-color: transparent;
    -webkit-box-shadow: 0;
            box-shadow: 0;
    opacity: 0;
  }
  75% {
    -webkit-box-shadow: 0px 0px 20px 20px yellow;
            box-shadow: 0px 0px 20px 20px yellow;
    background-color: yellow;
    opacity: .2;
  }
  100% {
    background-color: transparent;
    -webkit-box-shadow: 0;
            box-shadow: 0;
    opacity: 0;
  }
}

.ail-header-title_icon {
  position: relative;
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
}

.ail--sidebar-open .ail-header .ail-header-title_icon {
  -webkit-transform: translateY(0.5rem) rotate(180deg);
      -ms-transform: translateY(0.5rem) rotate(180deg);
          transform: translateY(0.5rem) rotate(180deg);
}

@media screen and (max-width: 991px) {
  .ail--sidebar-open .ail-header .ail-header-title_icon {
    -webkit-transform: translateY(3rem) rotate(180deg);
        -ms-transform: translateY(3rem) rotate(180deg);
            transform: translateY(3rem) rotate(180deg);
  }
}

.ail-header .ail-header-title_icon::before,
.ail-header .ail-header-title_icon::after {
  -webkit-transition: opacity .5s ease-in;
  -o-transition: opacity .5s ease-in;
  transition: opacity .5s ease-in;
  -webkit-transition-delay: .1s;
       -o-transition-delay: .1s;
          transition-delay: .1s;
  opacity: 0;
  height: 0;
  width: 0;
  content: ' ';
}

.ail--sidebar-open .ail-header .ail-header-title_icon::before {
  content: ' ';
  border-right: 1px white solid;
  position: absolute;
  height: 7rem;
  width: auto;
  -webkit-transform: translateX(50%);
      -ms-transform: translateX(50%);
          transform: translateX(50%);
  right: 50%;
  top: 1rem;
  opacity: 1;
}

.ail--sidebar-open .ail-header .ail-header-title_icon::after,
.ail-header-title_icon--loading::after {
  content: ' ';
  opacity: .4;
  border-radius: 50%;
  position: absolute;
  -webkit-transform: translateX(50%);
      -ms-transform: translateX(50%);
          transform: translateX(50%);
  bottom: 20%;
  right: 50%;
  height: 1rem;
  width: 1rem;
  z-index: 100;
  -webkit-animation: bulb 2s infinite cubic-bezier(0.55, 0.06, 0.68, 0.19);
          animation: bulb 2s infinite cubic-bezier(0.55, 0.06, 0.68, 0.19);
}


